import React, { Component } from 'react'
import { Accordion, Icon } from 'semantic-ui-react'

export default class Construction extends Component {
  state = { activeIndex: 0 }

  handleClick = (e, titleProps) => {
    const { index } = titleProps
    const { activeIndex } = this.state
    const newIndex = activeIndex === index ? -1 : index

    this.setState({ activeIndex: newIndex })
  }

  render() {
    const { activeIndex } = this.state

    return (
      <React.Fragment>
            <h3>Play Rules</h3>
            <tr>
                <td>7.</td>
                <td>
                    <p>Players begin the game with 40 life.</p>
                </td>
            </tr>
            <tr>
                <td>8.</td>
                <td>
                    <p>Commanders begin the game in the Command Zone. While a Commander is in the command zone, it may be cast, subject to the normal timing restrictions for casting creatures. Its owner must pay (2) for each time it was previously cast from the command zone; this is an additional cost.</p>
                </td>
            </tr>
            <tr>
                <td>9.</td>
                <td>
                    <p>If a Commander would be put into a library, hand, graveyard or exile from anywhere, its owner may choose to move it to the command zone instead.</p>
                </td>
            </tr>
            <Accordion style={{ marginBottom: '10px'}}>
                <Accordion.Title
                index={4}
                onClick={this.handleClick}
                >
                <Icon name='dropdown' />
                Details
                </Accordion.Title>
                <Accordion.Content active={activeIndex === 4}>

                <div className="ui container segment">
                    <ul>
                        <li>This is a replacement effect. It applies last and may apply multiple times to an event.</li>
                        <li>The creature never goes to the original destination zone and will not trigger abilities that trigger based on going there (e.g. "dies" triggers). </li>
                    </ul>
                </div>
                </Accordion.Content>
            </Accordion>
            <tr>
                <td>10.</td>
                <td>
                    <p>Being a Commander is not a characteristic [MTG CR109.3], it is a property of the card and tied directly to the physical card. As such, "Commander-ness" cannot be copied or overwritten by continuous effects. The card retains it's commanderness through any status changes, and is still a commander even when controlled by another player.</p>
                </td>
            </tr>
            <tr>
                <td>11.</td>
                <td>
                    <p>If a player has been dealt 21 points of combat damage by a particular Commander during the game, that player loses a game.</p>
                </td>
            </tr>
            <Accordion style={{ marginBottom: '10px'}}>
                <Accordion.Title
                index={5}
                onClick={this.handleClick}
                >
                <Icon name='dropdown' />
                Details
                </Accordion.Title>
                <Accordion.Content active={activeIndex === 5}>

                <div className="ui container segment">
                    <ul>
                        <li>This is an additional state based effect.</li>
                        <li>    Commander Damage is cumulative throughout the game; nothing can reduce the amount of damage a Commander has previously done to a player.</li>
                        <li>    Because it is a property of the card and not a characteristic of the game object, a card is still the same Commander even if it leaves the field and returns.</li>
                        <li>    While effects can raise a player's life total, it doesn't reduce the amount of damage previously taken from a Commander. (eg: Beacon of Immortality)</li>
                        <li>    Conversely, combat damage can be reduced, prevented, or replaced as it is taken, in which case it was never dealt and doesn't count towards the total taken from that Commander. (eg: Fog or Captain's Maneuver)</li>
                        <li>    Commander Damage is specific to each Commander/Player pairing, not combined across all Commanders.</li>
                        <li>    A player can lose if he or she is dealt 21 points of combat damage by his or her own Commander (ie: under someone else's control).</li>
                    </ul>
                </div>
                </Accordion.Content>
            </Accordion>
            <tr>
                <td>12.</td>
                <td>
                    <p>Commanders are subject to the Legend rule; a player cannot control more than one legend with the same name.</p>
                </td>
            </tr>
            <tr>
                <td>13.</td>
                <td>
                    <p>Abilities which bring card(s) you own from outside the game into the game (such as Wishes; Spawnsire of Ulamog; Karn, the Great Creator) do not function in Commander.</p>
                </td>
            </tr>
      </React.Fragment>
    )
  }
}