import React from 'react';

const Introduction = () => {
    return(
        <div className="ui container">
            <h2 class="ui header">
                Magic: The Gathering - Commander
            </h2>
            <p>This recreation of the <a href="https://mtgcommander.net/rules.php">Commander rules website</a> was made by Jordan Yu.</p>

            <p>Commander is a Magic:The Gathering format which emphasises multiplayer play, social interactions, interesting games, and creative deckbuilding.</p>

            <p>These are the official rules for Commander. Players often play with house rules, and are encouraged to, but this consensus version exists so that players know what to expect if they join a game outside their local play area.</p>
        </div>
    )
}

export default Introduction;