import React from 'react';

const Philosophy = () => {
    return(
        <React.Fragment>
            <h3>Philosophy</h3>
            <tr>
                <td>1.</td>
                <td>
                <p>Commander is designed to promote social games of magic. </p>
                <p>It is played in a variety of ways, depending on player preference, but a common vision ties together the global community to help them enjoy a different kind of magic. That vision is predicated on a social contract: an agreement which goes beyond these rules to includes a degree of interactivity between players.</p>
                <p>Players should aim to interact both during the game and before it begins, discussing with other players what they expect/want from the game. House rules or "fair play" exceptions are always encouraged if they result in more fun for the local community.</p>
                </td>
            </tr>
        </React.Fragment>

    )
}

export default Philosophy;