import React, { Component } from 'react'
import { Accordion, Icon } from 'semantic-ui-react'

export default class Construction extends Component {
  state = { activeIndex: 0 }

  handleClick = (e, titleProps) => {
    const { index } = titleProps
    const { activeIndex } = this.state
    const newIndex = activeIndex === index ? -1 : index

    this.setState({ activeIndex: newIndex })
  }

  render() {
    const { activeIndex } = this.state

    return (
      <React.Fragment>
        <h3>Deck Construction</h3>
      <tr>
        <td>2.</td>
        <td>
          <p>Players choose a legendary creature as the "Commander" for their deck.</p>
        </td>
      </tr>
      <Accordion style={{ marginBottom: '10px'}}>
        <Accordion.Title
          index={1}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          Details
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 1}>

        <div className="ui container segment">
          <p>Players may choose any legendary creature as their Commander, although some choices may be met with disapproval by other players. Two players in the same game may choose the same Commander, and other players may include that card in their Deck even if it's not their Commander. Commanders are subject to the Legend rule just like any other legendary creature.</p>
          <p>The Commander is the principle around which the deck is built. It is more easily available than other cards in the deck, and decks will ususally want to leverage their Commander's strengths in their plans.</p>
          <p>A deck's commander is also known as its "General" for historical reasons.</p>
        </div>
        </Accordion.Content>
      </Accordion>

      <tr>
        <td>3.</td>
        <td>
          <p>A card's colour identity is its colour plus the colour of any mana symbols in the card's rules text. A card's colour identity is established before the game begins, and cannot be changed by game effects.</p>
        </td>
      </tr>
      <Accordion style={{ marginBottom: '10px'}}>
        <Accordion.Title
          index={2}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          Details
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 2}>

        <div className="ui container segment">
          <div class="ui list">
            <p>
            Lands whose type includes swamp, island, plains, forest and/or mountain (e.g.: basic lands, shocklands, dual lands, Shadowmoor special-basics, etc) also have the corresponding colour identity. As such, they may not appear in a deck unless the Commander is also of the appropriate identity. 
            </p>
            <p>
            While hybrid mana symbols may be played with either colour mana, they contribute both colours to the card's colour identity. Therefore they may only be played with a Commander whose identity includes ALL of the hybrid symbols' colours. 
            </p>
            <p>
            Basic land words (swamp, forest, etc) in the text box of a card do NOT represent a coloured mana symbol. They are not restricted to a Commander of the same colour identity. 
            </p>
            <p>
            Reminder text is not included in the colour identity of a card. 
            </p>
            <p>
            The back face of a double-faced card is included when determining a card's colour identity. 
            </p>
          </div>
        </div>
        </Accordion.Content>
      </Accordion>
      <tr>
        <td></td>
        <td>
          <p>Cards in a deck may not have any colours in their color identity which are not shared with the commander of the deck. (The identity of each card in the deck must be a subset of the Commander's)</p>
        </td>
      </tr>
      <Accordion style={{ marginBottom: '10px'}}>
        <Accordion.Title
          index={3}
          onClick={this.handleClick}
        >
          <Icon name='dropdown' />
          Examples
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 3}>

        <div className="ui container segment">
          <div class="ui list">
            <p>A deck with Phelddagrif (Casting cost 1UWG) as the Commander may not contain any cards whose colour identity includes red or black.</p>
               <p>These cards would all be illegal in a Phelddagrif deck:</p>
            <ul>
                <li>Goblin Piker (Its mana cost contains a red mana symbol)</li>
                <li>Elves of Deep Shadow (Its ability contains a black mana symbol)</li>
                <li>Talisman of Dominance (All sorts of verbotten mana symbols)</li>
                <li>Life//Death (Three shall be the number of the colours)</li>
                <li>Degavolver (... right out)</li>
            </ul>

            <p>Our Phelddagrif couldn't use any of these lands:</p>

            <ul>
                <li>Underground River </li>
                <li>Godless Shrine</li>
                <li>Badlands</li>
                <li>Leechridden Swamp</li>
            </ul>

            <p>Phelddagrif IS allowed to use:</p>

            <ul>
                <li>Esper Panorama</li>
                <li>Shard Convergence</li>
            </ul>
          </div>
        </div>
        </Accordion.Content>
      </Accordion>

      <tr>
        <td>4.</td>
        <td style={{ marginBottom: '10px'}}>
          <p>A Commander deck must contain exactly 100 cards, including the Commander.</p>
        </td>
      </tr>
      <tr style={{ marginBottom: '10px'}}>
        <td>5.</td>
        <td>
          <p>With the exception of basic lands, no two cards in the deck may have the same english name. Some cards (e.g. Relentless Rats) may have rules text that overrides this restriction.</p>
        </td>
      </tr>
      </React.Fragment>
    )
  }
}
